@keyframes pulsate {
  0% {
    transform: scale(.95);
  }

  5% {
    transform: scale(1.1);
  }

  39% {
    transform: scale(.85);
  }

  45% {
    transform: scale(1);
  }

  60% {
    transform: scale(.95);
  }

  100% {
    transform: scale(.9);
  }
}

